import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Index from './views/Index.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Application from './views/nav2/Application.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import SysLog from './views/nav3/SysLog.vue'
import AdminLog from './views/nav3/AdminLog.vue'
import UserLog from './views/nav3/UserLog.vue'
import GuidePageConfig from './views/nav4/GuidePageConfig.vue'
import IntegralConfig from './views/nav4/IntegralConfig.vue'
import SubscribeConfig from './views/nav4/SubscribeConfig.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        leaf: true,//
        children: [
            { path: '/Index', component: Index, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',//class
        leaf: true,//
        children: [
            // { path: '/main', component: Main, name: '', hidden: true },
            { path: '/Application', component: Application, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/SysLog', component: SysLog, name: '' },
            { path: '/AdminLog', component: AdminLog, name: '' },
            { path: '/UserLog', component: UserLog, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/GuidePageConfig', component: GuidePageConfig, name: '' },
            { path: '/IntegralConfig', component: IntegralConfig, name: '' },
            { path: '/SubscribeConfig', component: SubscribeConfig, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/page4', component: Page4, name: '' },
            { path: '/Main', component: Main, name: '' },
            { path: '/Form', component: Form, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/user', component: user, name: '4' },
            { path: '/page5', component: Page5, name: '5' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


